    :progress="aligerante.progress"
    :name="aligerante.nombre"
    :depto="aligerante.depto"
    :days="aligerante.dias_laborados"
    :m3="aligerante.m3_persona"
    :bonus_p="aligerante.bono_persona"
    :bonus_tot="aligerante.bono_depto"
    :asis="aligerante.asistencia"
    :extra="aligerante.$_extra_m3"
    :bonus_prod="aligerante.bono_productividad"
    :bonus_goals="aligerante.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Aligerante',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'aligerante',
            userCode: this.$route.params.user,
            city: 'juarez'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
